.loading_body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.loading_loader {
  position: relative;
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: animateColor 7.2s linear infinite;
}
@keyframes animateColor {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}

.loading_span {
  position: absolute;
  transform-origin: 150px;
  transform: translateX(-150px) rotate(calc(var(--i) * 30deg));
  filter: drop-shadow(0 0 5px #3cc2ff) drop-shadow(0 0 15px #3cc2ff) drop-shadow(0 0 30px #3cc2ff);
}

.loading_i {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: relative;
  color: #3cc2ff;
  animation: rotate-stars 2.4s linear infinite;
  animation-delay: calc((var(--i) * -0.2s));
}

@keyframes rotate-stars {
  0% {
    transform: rotate(0deg) scale(0);
  }
  50% {
    transform: rotate(180deg) scale(3);
  }
  100% {
    transform: rotate(360deg) scale(0);
  }
}
.loading_loader span::before {
  content: "\f005";
  position: absolute;
  font-family: fontAwesome;
  font-size: 0.75em;
  color: #131a1c;
  animation: rotate-particle 2.4s linear infinite;
  animation-delay: calc(var(--i) * -0.2s);
}
@keyframes rotate-particle {
  0% {
    scale: 1;
    opacity: 0;
    rotate: 0deg;
  }
  50% {
    scale: 1;
    opacity: 1;
    rotate: 180deg;
  }
  100% {
    scale: 0;
    opacity: 0;
    rotate: 360deg;
    filter: drop-shadow(-150px 0 #3cc2ff) drop-shadow(150px 0 #3cc2ff) drop-shadow(0 150px #3cc2ff);
  }
}